import React, { Component } from "react";
import "./App.css";
import MainComponent from "./components/MainComponent";

export default class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      covid: [],
      isLoaded: false,
    };
  }

  componentDidMount() {
    fetch("https://covid-india-cases.herokuapp.com/states/")
      .then((data) => data.json())
      .then((data) => {
        const newData = data.sort(function (a, b) {
          return b.noOfCases - a.noOfCases;
        });
        return this.setState({ covid: newData, isLoaded: true });
      });
  }

  render() {
    if (!this.state.isLoaded) {
      return <div id="loader"></div>;
    }

    return (
      <React.Fragment>
        <MainComponent covid={this.state.covid} />
      </React.Fragment>
    );
  }
}
