import React, { Component } from "react";

export default class Footer extends Component {
  render() {
    const date = new Date().toLocaleString();

    return (
      <div className="container">
        <div className="links">
          As of : {date.substring(0, 9)}
          <hr />
          <a href="https://covid-india-cases.herokuapp.com/states/">
            States Statistic API
          </a>
          <hr />
          <a href="https://github.com/immanishbainsla/react-covid-19">
            Source Code
          </a>
          <hr />
          <a href="https://immanishbainsla.github.io">
            Developed by : Manish Bainsla
          </a>
          <hr />
        </div>
      </div>
    );
  }
}
