import React, { Component } from "react";
import "../App.css";
import Footer from "./Footer";

export default class MainComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    let total = 0;
    let active = 0;
    let cured = 0;
    let deceased = 0;

    const covid = this.props.covid.map((item) => {
      let ctotal = item.noOfCases;
      let cdeceased = item.deaths;
      let ccured = item.cured;
      let cactive = ctotal - (cdeceased + ccured);
      total += ctotal;
      active += cactive;
      cured += ccured;
      deceased += cdeceased;

      return (
        <tr>
          <th scope="row" className="col text-dark">
            {item.state}
          </th>
          <td className="col text-info">{ctotal}</td>
          <td className="col text-warning">{cactive}</td>
          <td className="col text-success">{ccured}</td>
          <td className="col text-danger">{cdeceased}</td>
        </tr>
      );
    });

    return (
      <div className="container">
        <div className="container-fluid shadow-lg bg-white rounded mt-2 pt-3 border">
          <header className="mb-2 pb-1 bg-dark text-white text-center display-4 rounded">
            Covid - 19 Statistics
          </header>
          <section className="p-2">
            <div className="d-flex justify-content-around">
              <button className="btn btn-sm btn-outline-info">
                Total : {total}
              </button>
              <button className="btn btn-sm btn-outline-warning">
                Active : {active}
              </button>
              <button className="btn btn-sm btn-outline-success">
                Cured : {cured}
              </button>
              <button className="btn btn-sm btn-outline-danger">
                Deceased : {deceased}
              </button>
            </div>
            <div className="p-2 mt-4">
              <table class="table table-hover">
                <thead>
                  <tr>
                    <th scope="col" className="col text-dark">
                      State / UTs
                    </th>
                    <th scope="col" className="col text-info">
                      Total
                    </th>
                    <th scope="col" className="col text-primary">
                      Active
                    </th>
                    <th scope="col" className="col text-success">
                      Cured
                    </th>
                    <th scope="col" className="col text-danger">
                      Deceased
                    </th>
                  </tr>
                </thead>
                <tbody>{covid}</tbody>
              </table>
            </div>
          </section>
        </div>
        <Footer />
      </div>
    );
  }
}
